import { auth } from "../utils/auth"
import JwtDecode from 'jwt-decode'
import Cadastro from "../containers/Cadastro/Cadastro"


const CadastroPage = ({ data, model }) => {


    return <Cadastro data={data} model={model} />

}

CadastroPage.getInitialProps = async (ctx) => {
    const token = auth(ctx)
    if (token) {
        const { type, refId } = JwtDecode(token)



        const res = await fetch(`${process.env.API_URL}/api/${type}/${refId}`)
        const data = await res.json();
        return { data, model: type }
    }
    return {}
}

export default CadastroPage;